$susy: (use-custom: (em: true), columns: 12, gutters: 0, gutter-position: split, global-box-sizing: border-box);

@include border-box-sizing;
$border-box-sizing : true;
$gutters: 0;

.fullWidth {
	min-width: 100%; }
.halfWidth {
	min-width: 50%; }
.parent {
	display: flex; }
.row {
	flex-direction: row; }
.wrap {
	flex-wrap: wrap; }
.nowrap {
	flex-wrap: nowrap; }
.column {
	flex-direction:  column; }

.baseChild {
	flex: 1 0 20%; }
.h-around {
	justify-content: space-around; }
.h-between {
	justify-content: space-between; }
.h-start {
	justify-content: flex-start; }
.h-end {
	justify-content: flex-end; }
.h-centered {
	justify-content: center; }
.h-s-end {
	justify-self: flex-end; }
.h-s-start {
	justify-self: flex-start; }
.h-s-center {
	justify-self: center; }
.v-centered {
	align-items: center; }
.v-start {
	align-items: flex-start; }
.v-end {
	align-items: flex-end; }
.v-s-end {
	align-self: flex-end; }
.v-s-start {
	align-self: flex-start; }
.v-s-center {
	align-self: center; }
.centered {
	justify-content: center;
	align-items: center; }
