.visible-hidden {
	white-space: nowrap;
	overflow: hidden;
	clip: rect(1px, 1px, 1px, 1px);
	width: 1px;
	height: 1px;
	position: absolute; }
[hidden] {
	display: none; }
.materialCascadingShadow {
	box-shadow: 0 4px 15px  $shadow; }
// .lowCascadingShadow
// 	box-shadow: 0 4px 15px -1px $shadow
.visible-hidden-smLess {
	@include breakpoint($sm-less) {
		white-space: nowrap;
		overflow: hidden;
		clip: rect(1px, 1px, 1px, 1px);
		width: 1px;
		height: 1px;
		position: absolute; } }


.visible-hidden-xs {
	@include breakpoint($xs) {
		white-space: nowrap;
		overflow: hidden;
		clip: rect(1px, 1px, 1px, 1px);
		width: 1px;
		height: 1px;
		position: absolute; } }



.registrationButton {


	&:disabled {
		background-color: $gray;
		&__refer {
			&:hover {
				color: $darkGray $i; } }


		&:hover {
			color: $darkGray $i;
			box-shadow: none; } }





	// &__refer
	// 	color: $white $i
	// 	padding: 1em 0
	// 	display: block
	// 	background-image: none $i
	// 	&:hover
	// 		color: $white $i
	//// background-image: none $i
	&:hover, &:focus, &:active {
		@extend %flyShadow;
		z-index: 3; } }


.textShadow {
	text-shadow: -2px 1px 13px rgba(0, 0, 0, 0.35); }

.cascadeShadow {
	box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.4); }
.imageContainer {
	&, &__image {
		transition: .3s ease-in-out;
		width: span(12); }

	&__image {
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover; } }


.closeButton {
	background-color: $darkGray;
	&, &::after {
		@extend %round; }
	@extend %absolute;
	border: 0;
	z-index: 1;
	width: em(18);
	height: em(18);
	right: em(-8);
	top: em(-9);
	font-size: em(28);
	box-shadow: -2px 2px 2px $shadow;
	will-change: transform;
	transition: transform .2s ease-in-out, box-shadow .3s ease;
	&:hover {
		transform: rotate(25deg);
		box-shadow: 0 0 2px $shadow; }
	&__bar {
		@extend %absolute;
		width: em(7);
		height: em(1.1);
		border-radius: 2px;
		background-color: $white;
		z-index: 3;
		left: em(9.7, 28);
		top: em(15, 28);
		&_right {
			transform: rotate(-45deg); }
		&_left {
			transform: rotate(45deg); } }

	&::after {
		content: "";
		@extend %absolute;
		z-index: 2;
		width: em(11);
		height: em(11);
		top: em(6.3, 28);
		left: em(6.2, 28);
		@extend %shadow;
		background-color: $orange; } }




