.controller {
	max-width: em(300);
	min-width: none;
	width: 100%;
	@extend %relative;
	&:not(:first-child) {
		margin-top: em(12.35); }
	&_required {
		color: $red; }
	&__icon {
		margin-bottom: .15em; }
	&_small {
		max-width: em(175); }
	&__paragraph {
		@extend %absolute;
		bottom: .70em;
		left: 100.5%;
		font-weight: bold;
		left: calc(100% + .2em);
		white-space: nowrap;
		display: block;
		overflow: hidden;
		text-overflow: ellipsis; }

	&__input {
		box-shadow: 0 0 15px $shadow;
		border-radius: 3px;
		min-width: span(12);
		max-width: span(12);
		font-size: em(18, 21);
		padding: em(16) em(12.5);
		@include round(em(2, 21));
		background-color: #C4C4C4;
		border-width: 2px;
		border-style: solid;
		transition: box-shadow .3s ease-in;
		&_whiteBg, &_textarea {
			background-color: $white; }
		&_textarea {
			min-height: em(100); }

		&_red {
			border-color:  $red; }
		&:focus {
			box-shadow: 0 7px 15px -1px $shadow;
			&:invalid {
				border-color: #5C1D31; }

			&:valid {
				border-color: #437A67; } }



		&_select {
			padding: 0 em(9);
			& hr {
				display: none; }
			& button {
				right: -9px $i; } } }

	// 	padding: .5em
	// 	margin-top: .5em
	// 	max-height: em(200)



	&__option {
		// background-color: #C4C4C4 $i
		color: #4F4F4F $i;
		& .colorContainer {
			margin-left: 0;
			&__name {
				opacity: 1; }
			&__color {
				margin: 0 .5em 0 0; } }
		&:hover {
			background-color: #4F4F4F $i;
			color: #F2F2F2 $i; } }

	&Icon {
		margin-bottom: em(2.3); }
	&__error {
		color: $red;
		margin-top: em(6, 16);
		font-size: em(16, 18);
		text-align: left; }
	&__label {
		align-self: flex-end;
		font-size: em(18); }

	&__checkbox {
		-webkit-appearance: none;
		align-self: center;
		cursor: pointer;
		// padding: em(9)
		height: em(26);
		width: em(26);
		border: 2px solid $purple;
		background-color: $gray;
		max-width: span(1);
		margin-right: span(1) / 2;
		display: inline-block;
		position: relative;
		@include round(em(2));
		&:checked:active {}


		&:checked:after {
			content: '\2714';
			// font-size: em(15)
			position: absolute;
			top: em(1);
			left: em(3);
			color: $darkGray; } }


	&__checkboxLabel {
		color: $white;
		padding: .5em 0 .8em 1em;
		max-width: span(10);
		background-color: $red;
		@include round(em(2)); } }



.restrictBlock {
	margin: auto;
	max-width: em(1350); }


.button {
	max-width: em(152);

	box-shadow: 0 0 15px $shadow;
	color: $darkGray;
	border: 2px solid $darkGray;
	border-radius: em(2);
	padding: .75em .5em;
	background-color: transparent;
	min-width: em(95, 10);
	@include round(em(2));
	&_cian {
		background-color: $cian; }

	&_black {
		background-color: $darkGray; }
	&_black, &_darkBlue, &_cian {
		color: $white; }
	&_darkBlue {
		background-color: $darkBlue; }
	&_orange {
		background-color: $orange; }
	@include flyShadow; }

.orderButtons {
	@extend %relative; }
.shown_element {
	opacity: 1; }
.hidden_element {
	opacity: 0; }

.cart {
	font-size: em(24);
	width: em(300);
	margin-top: em(1, 24);
	transition: opacity .2s;
	box-shadow: inner 0 0 14;
	z-index: 99;
	@extend %relative;
	@include breakpoint($xxs) {
		width: 75vw; }
	&_order {
		width: 	span(12);
		margin-top: em(21, 24);
		& .cart__hint {
			width: 98%;
			border-right: 0; } }
	&_hover {
		&_up, &_bottom {
			@extend %absolute;
			will-change: transform;
			@extend %flyShadow;
			cursor: move;
			z-index: 100; }
		&_bottom {
			top: 105%;
			right: 0; }
		&_up {
			bottom: 110%;
			left: 0; } }

	&__hint {
		@extend %absolute;
		left: 0;
		left: calc(0% + 3px);

		top: 0;
		top: calc(0% + 3px);
		border: 2px solid $darkBlue;
		border-top: 0;
		border-left: 0;
		color: $white;
		font-size: em(16, 24) $i;
		padding: .5em;
		text-align: center;
		width: span(5);
		margin: 0 $i;
		background-color: $orange;
		z-index: -1;
		opacity: 0;
		will-change: opacity;
		transition: opacity .2s ease;
		&_shown {
			z-index: 105; }
		&_empty,  &_shown {
			opacity: 1; }
		&_empty {
			z-index: 1; } } }



