.fading {
	&-appear {
		opacity: 0.1;
		will-change: opacity;
		&.fading-appear-active {
			transition: opacity 1s ease-in;
			opacity: 1; } }
	&-enter {
		opacity: 0.1;
		will-change: opacity;
		&.fading-enter-active {
			transition: opacity .5s ease-in;
			opacity: 1; } }
	&-leave {
		opacity: 1;
		will-change: opacity;
		&.fading-leave-active {
			transition: opacity .3s ease-in;
			opacity: 0.1; } } }
.main {
	position: relative; }
.translate {
	&-appear {
		transform: translateX(-200%);
		will-change: transform;
		&.translate-appear-active {
			transition: transform .7s ease-in;
			transform: translateX(0); } }
	&-enter {
		transform: translateX(-200%);
		will-change: transform;
		&.translate-enter-active {
			transition: transform .7s ease-in;
			transform: translateX(0); } }
	&-exit {

		// margin-top: 4.6875em
		position: absolute;

		will-change: opacity;
		opacity: 1;
		&.translate-exit-active {
			transition: opacity .3s ease-in;
			opacity: 0;
 } } }			// transform: translate(250%);

.moreRefer {
	font-size: em(18);
	@extend %absolute;
	bottom: em(29.34);
	right: em(34.41);
	&_product {
		position: absolute;
		font-size: 1rem;
		top: 0;
		left: 0;
		padding: .1rem;
		background-color: $orange;
		max-height: 2rem;
		max-width: 6rem;
		color: $darkGray; } }
